<template>
  <div
    v-if="showSizer || showElevator"
    :class="optsClasses"
  >
    <div
      v-if="showSizer"
      :class="sizerClasses"
    >
      <i-select
        v-model="currentPageSize"
        :size="size"
        :placement="placement"
        :transfer="transfer"
        @on-change="changeSize"
      >
        <i-option
          v-for="item in pageSizeOpts"
          :key="item"
          :value="item"
          style="text-align:center;"
        >
          {{ item }} {{ t('i.page.page') }}
        </i-option>
      </i-select>
    </div>
    <div
      v-if="showElevator"
      :class="ElevatorClasses"
    >
      {{ t('i.page.goto') }}
      <label>
        <input
          type="text"
          :value="scoreCurrent"
          autocomplete="off"
          spellcheck="false"
          @keyup="onKeyup"
        >
      </label>
      {{ t('i.page.p') }}
    </div>
  </div>
</template>
<script>
import iSelect from '../select/select.vue';
import iOption from '../select/option.vue';
import Locale from '../../mixins/locale';

const prefixCls = 'ivu-page';

function isValueNumber(value) {
  return /^[1-9][0-9]*$/.test(`${value}`);
}

export default {
  name: 'PageOption',
  components: {iSelect, iOption},
  mixins: [Locale],
  props: {
    pageSizeOpts: Array,
    showSizer: Boolean,
    showElevator: Boolean,
    current: Number,
    scoreCurrent: Number,
    pageSize: Number,
    allPages: Number,
    isSmall: Boolean,
    placement: String,
    transfer: Boolean,
  },
  data() {
    return {
      currentPageSize: this.pageSize,
    };
  },
  computed: {
    size() {
      return this.isSmall ? 'small' : 'default';
    },
    optsClasses() {
      return [`${prefixCls}-options`];
    },
    sizerClasses() {
      return [`${prefixCls}-options-sizer`];
    },
    ElevatorClasses() {
      return [`${prefixCls}-options-elevator`];
    },
  },
  watch: {
    pageSize(val) {
      this.currentPageSize = val;
    },
  },
  methods: {
    changeSize() {
      this.$emit('on-size', this.currentPageSize);
    },
    changePage(event) {
      let val = event.target.value.trim();
      let page = 0;

      if (isValueNumber(val)) {
        val = Number(val);

        if (val !== this.current) {
          const allPages = this.allPages;

          if (val > allPages) {
            page = allPages;
          } else {
            page = val;
          }
        }
      } else {
        page = 1;
      }

      if (page) {
        this.$emit('on-page', page);
        event.target.value = page;
      }
    },
    onKeyup(event) {
      if (event.key === 'Enter') {
        this.changePage(event);
      }
    },
  },
};
</script>
